<template lang="html">
  <div class="_full_router cardsAddBody">
    <custHeader :title="title"></custHeader>
    <div class="bankCardsAdd-inputs">

      <mt-field label="银行" placeholder="请选择银行" v-model="bank" v-lfoucs="showBank"></mt-field>

      <!-- <mt-field label="持卡人" placeholder="请输入真实姓名" type="text" v-model="cardsName" v-validtype="rules.cardsName" :state="rules.cardsName.state"></mt-field> -->

      <mt-field label="银行卡号" placeholder="请输入银行卡号" type="number" v-model="cradsNumber" v-validtype="rules.cradsNumber" :state="rules.cradsNumber.state"></mt-field>

      <mt-field label="手机号" placeholder="请输入手机号" type="text" v-model="mb" disabled></mt-field>



      <!-- <mt-field label="身份证号" placeholder="请输入身份证号" type="number" v-model="idNumber" v-validtype="rules.idNumber" :state="rules.idNumber.state"></mt-field> -->

      <!-- <div class="" style="position: fixed;bottom: 102px;left: 20px;">
        <input type="checkbox" name="" @click="agree" value="isAgree"><a href="#">我已阅读并同意《入金授权书》</a></input>
      </div> -->
      

      <mt-button type="danger" size="large" class="bind" @click="cardBind" >确认绑定</mt-button>
    </div>

    <mt-popup
        v-model="bankVisible"
        style="width:100%"
        position="bottom"
      >
        <button class="btn" @click="dissBank">取消</button>
        <button class="btn" @click="sureBank">确认</button>
        <mt-picker :slots="bankSlots" class="acctTypepicker" valueKey="name" @change="bankChange" :visible-item-count="3"></mt-picker>
      </mt-popup>

  </div>
</template>

<script>
import {MessageBox} from 'mint-ui'
import cookieUtil from 'js-cookie'
import custHeader from  'components/custHeader'
export default {
  data(){
    return {
      title:'添加银行卡',
      mb:"",
      bank:'',
      bankCode:'',
      // cardsName:'',
      cradsNumber:'',
      // idNumber:'',
      bankVisible:false,
      isAgree:true,
      bankSlots:[{
        flex:2,
        // values:[{value:'0',name:'中国工商银行'},{value:'1',name:'杭州银行'},{value:'2',name:'浙商银行'}],
        values:[],
        className:"slots"
      }],
      rules:{
        // cardsName:{
        //   str:"required",
        //   state:""
        // },
        cradsNumber:{
          str:"required",
          state:""
        },
        // idNumber:{
        //   str:"required",
        //   state:""
        // },
      },
    }
  },
  created(){
    var self = this;
    axiosUtil.upCltBankCodeQry().then(function(data){
      var data = data.data.data;
      for (var i = 0; i < data.length; i++) {
        self.bankSlots[0].values.push({value:data[i].orgCode,name:data[i].orgFullName})
      }
      this.bankCode = this.bankSlots[0].values[0].value
    })

    this.mb = cookieUtil.get("mb")
  },
  methods:{
    showBank:function(){
      this.bankVisible = true
    },
    bankChange:function(picker,value){
    //  console.log(value)
     if(value[0]) {
       this.bank = value[0].name
       this.bankCode =value[0].value
     }

    // this.acctTypeValue = value[0].value
   },
   dissBank:function(){
     this.bankVisible = false
   },
   sureBank:function(){
     this.bankVisible = false
   },
   agree:function(){
     this.isAgree = !this.isAgree
   },
   cardBind:function(){
     var self = this;
     // var isReal = true;
     axiosUtil.qrySafeQuestion().then(data=>{
      var data = data.data;
      if(data.data == undefined){
        MessageBox.alert("您还没有设置安保问题，请先去设置安保问题").then(action => {
            router.push('/personal/setProblem')
          })
      }else{
        axiosUtil.getRealnameAuthQry().then(data=>{
          var data=data.data;
          if(data.errorcode!==0){
            // return false
            // isReal = false
            MessageBox.alert("您还没有实名认证过，请先去实名认证").then(action => {
              router.push('/personal/realnameAuth')
            })
          }else{
            if(this.rules.cradsNumber.state == 'success'){
             axiosUtil.upCltBankAcctOpen({bankCode:self.bankCode,currency:0,bankAcct:self.cradsNumber,mb:self.mb}).then(function(data){

               if(data.errorcode){
                 if(data.errormsg == "请先实名认证"){
                   MessageBox.alert("您还没有实名认证过，请先去实名认证").then(action => {
                     router.push('/personal/realnameAuth')
                   })
                 }else{
                   MessageBox("提示", data.errormsg);
                 }
                 return false
               }else{
                 MessageBox.alert("绑定成功").then(action => {
                   router.push("/personal")
                 })
               }
             })
           }else{
             MessageBox("提示", "请将信息填写完整！");
           }
          }
        })
      }
     })


     

     
   }
  },
  components:{
     custHeader
  },
}
</script>

<style lang="css">
.cardsAddBody{
  background-color: #eee;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 5;
}
.supportBanks{

}
.btn{
  width: 49%;
  background-color: #fff;
  border: 0;
  height: 40px;
  font-size: 20px;
  outline: 0;
}
.bind{
  position: fixed;
  bottom: 0%;
  margin: 0 3% 0 3%;
  width: 94%;
}
</style>



